
import dayjs from "dayjs";
import XHeader from 'vux/src/components/x-header/index.vue'

import EProgress from "@/components/progress";
import Query from "@/components/filter";
import { state, classify, sourceList } from "@/common/js/classify";
import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
export default {
data() {
return {
title: classify[0].title,
total: 0,
dataList: [],
startDate: "",
endDate: "",
queryType: 1,
unit: "个",
url: state[0].url,
defaultDate: {
beginDate: "",
endDate: ""
},
selUserId: this.$store.state.CurrentUserID,
stateIndex: 0,
sourceList: sourceList
};
},
created() {
this.$store.state.isMyClient = false;
this.defaultDate.beginDate = dayjs()
.startOf("month")
.format("YYYY-MM-DD");
this.defaultDate.endDate = dayjs().format("YYYY-MM-DD");
this.startDate = this.dateFormat(dayjs().startOf("month"), true);
this.endDate = this.dateFormat();
this.getData();
},
methods: {
getData() {
this.$vux.loading.show({
text: "加载中"
});
this.getAxios(this.url, {
startDate: this.startDate,
endDate: this.endDate,
queryType: this.queryType,
selUserId: this.selUserId
})
.then(res => {
this.$vux.loading.hide();
this.dataList = res.Data.List;
this.total = res.Data.TotalCount;
})
.catch(err => {
this.$vux.loading.hide();
});
},
dateFormat(date, type) {
if (type) {
return (
(date
? dayjs(date).format("YYYY-MM-DD")
: dayjs().format("YYYY-MM-DD")) + " 00:00:00"
);
} else {
return (
(date
? dayjs(date)
.add(1, "day")
.format("YYYY-MM-DD")
: dayjs()
.add(1, "day")
.format("YYYY-MM-DD")) + " 00:00:00"
);
}
},
statClick(data) {
if (data.type === 1) {
this.unit = classify[data.index].unit;
this.queryType = classify[data.index].id;
this.title = classify[data.index].title;
} else {
this.url = state[data.index].url;
this.stateIndex = data.index;
}
this.getData();
},
dateQuery(date) {
let { beginDate, endDate } = date;
this.startDate = this.dateFormat(beginDate, true);
this.endDate = this.dateFormat(endDate);
this.getData();
},
goto(id) {
let name =
this.queryType > 3 ? (this.queryType > 5 ? "汇款" : "订单") : "";
this.$router.push({
path: "/classify/statDetail",
query: {
sIndex: this.stateIndex,
queryType: this.queryType,
param: id,
startDate: this.startDate,
endDate: this.endDate,
name,
selUserId: this.selUserId
}
});
},
selectedUser(id) {
this.selUserId = id;
this.getData();
}
},
filters: {
ratio(num, total) {
return parseFloat(((num / total) * 100).toFixed(2));
},
sourceToStr(key, stateIndex) {
try {
return stateIndex == 5 ? sourceList[key - 1].value : key;
} catch (e) {}
}
},
components: {
XHeader,
EProgress,
Query,
Scroll,
NoData
}
};



